import ExtensionPage from 'flarum/components/ExtensionPage';
import LoadingIndicator from 'flarum/components/LoadingIndicator';
import Button from 'flarum/components/Button';
import GuaGuaLeSettingsAddModal from './GuaGuaLeSettingsAddModal';
import GuaGuaLeSettingsListItem from './GuaGuaLeSettingsListItem';

export default class SettingsPage extends ExtensionPage {
  oninit(attrs) {
    super.oninit(attrs);
    this.loading = false;
    this.loading_raffle = true;
    this.guagualeList = [];
    this.loadResults();
  }

  content() {
    let loading_raffle;

    if(this.loading_raffle){
      loading_raffle = LoadingIndicator.component({ size: "large" });
    }

    return (
      <div className="ExtensionPage-settings FlarumBadgesPage">
        <div className="container">
          {this.buildSettingComponent({
            type: 'string',
            setting: 'wusong8899-guaguale.guagualeDisplayName',
            label: app.translator.trans('wusong8899-guaguale.admin.settings.guaguale-display-name'),
            placeholder:app.translator.trans('wusong8899-guaguale.admin.settings.guaguale-display-name-default')
          })}

          <div className="Form-group">{this.submitButton()}</div>

          <div style="padding-bottom:10px">
            <Button className={'Button'} onclick={() => app.modal.show(GuaGuaLeSettingsAddModal)}>
              {app.translator.trans('wusong8899-guaguale.admin.guaguale-add')}
            </Button>
          </div>

          <div style="padding:10px 0px 20px 0px">
            {this.guagualeList.map((guagualeData) => {
              return (
                <div style="padding-top:5px">
                  {GuaGuaLeSettingsListItem.component({ guagualeData })}
                </div>
              );
            })}
          </div>

          {loading_raffle && <div className="GuaGuaLe-loadMore">{loading_raffle}</div>}

        </div>
      </div>
    );
  }

  parseResults(results) {
    [].push.apply(this.guagualeList, results);
    this.loading_raffle = false;
    m.redraw();
    return results;
  }

  loadResults() {
    return app.store
      .find("guagualeList")
      .catch(() => {})
      .then(this.parseResults.bind(this));
  }
}
