import Modal from 'flarum/components/Modal';
import Button from 'flarum/components/Button';

export default class GuaGuaLeSettingsDeleteModal extends Modal {
  static isDismissible = false;

  oninit(vnode) {
    super.oninit(vnode);
    this.guagualeData = this.attrs.guagualeData;
    this.loading = false;
  }

  className() {
    return 'checkInResultModal Modal--small';
  }

  title() {
    return app.translator.trans('wusong8899-guaguale.admin.settings.guaguale-item-delete-confirmation');
  }

  content() {
    //

    return (
      <div className="Modal-body">
        <div className="Form-group" style="text-align: center;">
          {Button.component(
            {
              className: 'Button Button--primary',
              type: 'submit',
              loading: this.loading,
            },
            app.translator.trans('wusong8899-guaguale.admin.guaguale-data-confirm')
          )}&nbsp;
          {Button.component(
            {
              className: 'Button guagualeButton--gray',
              loading: this.loading,
              onclick: () => {
                this.hide();
              }
            },
            app.translator.trans('wusong8899-guaguale.admin.guaguale-data-cancel')
          )}
        </div>
      </div>
    );
  }

  onsubmit(e) {
    e.preventDefault();

    this.loading = true;

    const colorBackgroundValue = $("#ColorBackgroundSelection").val();
      this.guagualeData.save({
        activated:0,
      })
      .then(
        () => location.reload(),
        (response) => {
          this.loading = false;
          this.handleErrors(response);
        }
      );
  }
}
