import Modal from 'flarum/components/Modal';
import Button from 'flarum/components/Button';
import Select from 'flarum/common/components/Select';
import Stream from 'flarum/utils/Stream';
import Alert from 'flarum/common/components/Alert';

export default class GuaGuaLeSettingsAddModal extends Modal {
  static isDismissible = false;

  oninit(vnode) {
    super.oninit(vnode);
    this.guagualeData = this.attrs.guagualeData;
    this.settingType = "add";

    let guagualeSettings = {ratio:{}};
    let guagualeSettingsList = [];
    this.defaultAmount = 200;
    this.winChance = Stream(0);
    this.moneyName = app.forum.attribute('antoinefr-money.moneyname') || '[money]';

    if(this.guagualeData){
      this.settingType = "edit";
      this.guagualeTitle = Stream(this.guagualeData.title());
      this.guagualeDesc = Stream(this.guagualeData.desc());
      this.guagualeAmount = Stream(this.guagualeData.amount());
      this.guagualeCost = Stream(this.guagualeData.cost());
      this.guagualeColor = Stream(this.guagualeData.color());
      this.guagualeImage = Stream(this.guagualeData.image());
      guagualeSettings = JSON.parse(this.guagualeData.settings());
    }else{
      this.guagualeTitle = Stream("");
      this.guagualeDesc = Stream("");
      this.guagualeCost = Stream(1);
      this.guagualeAmount = Stream(this.defaultAmount);
      this.guagualeColor = Stream("");
      this.guagualeImage = Stream("");
      guagualeSettings.ratio[0] = this.defaultAmount;
    }
    
    this.guagualeSettingsList = this.getGuaGuaLeSettingList(guagualeSettings);
    this.guagualeSettings = guagualeSettings;
    this.checkGuaGuaLeAmount();
  }

  onColorBackgroundSelectionReady(vnode) {
    if(this.guagualeImage()){
      $("#ColorBackgroundSelection").val("background");
    }else{
      $("#ColorBackgroundSelection").val("color");
    }
  }

  className() {
    return 'Modal--Large';
  }

  title() {
    return this.settingType==="add"?app.translator.trans('wusong8899-guaguale.admin.guaguale-add'):app.translator.trans('wusong8899-guaguale.admin.guaguale-edit');
  }

  content() {
    const guagualeColorPreviewStyle = "position: absolute;right: 12px;top: 6px;width:24px;height:24px;background-color:"+this.guagualeColor()+";border-radius: var(--border-radius);";
    const GuaGuaLeBackgroundContainerStyle = "display:"+(this.guagualeImage()?"":"none");
    const GuaGuaLeColorContainerStyle = this.guagualeImage()?"display:none":"";

    return (
      <div className="Modal-body">
        <div className="Form">
          <div className="Form-group" style="text-align: center;">
            <div>
              <div class="GuaGuaLeSettingsLabel">{app.translator.trans('wusong8899-guaguale.admin.settings.guaguale-item-title')}</div>
              <input maxlength="255" required className="FormControl" bidi={this.guagualeTitle} />
              <div class="GuaGuaLeSettingsLabel">{app.translator.trans('wusong8899-guaguale.admin.settings.guaguale-item-desc')}</div>
              <textarea maxlength="255" required className="FormControl" bidi={this.guagualeDesc} />
              <div class="GuaGuaLeSettingsLabel">{app.translator.trans('wusong8899-guaguale.admin.settings.guaguale-item-cost')}</div>
              <input required className="FormControl" type="number" step="1" min="1" bidi={this.guagualeCost} />
              <div class="GuaGuaLeSettingsLabel">{app.translator.trans('wusong8899-guaguale.admin.settings.guaguale-item-amount')}</div>
              
              {this.settingType==="add" && (
                <input required className="FormControl" type="number" step="1" min="1" bidi={this.guagualeAmount} onblur={() => this.checkGuaGuaLeAmount()} />
              )}
              {this.settingType==="edit" && (
                <input disabled className="FormControl" type="number" step="1" min="1" bidi={this.guagualeAmount} />
              )}

              <div class="GuaGuaLeSettingsLabel">{app.translator.trans('wusong8899-guaguale.admin.settings.guaguale-item-color-background')}</div>
              <div style="text-align: left;display: flex;gap: 10px;">
                <span class="Select" oncreate={this.onColorBackgroundSelectionReady.bind(this)}>
                  <select id="ColorBackgroundSelection" class="Select-input FormControl" buttonclassname="Button" onchange={(e) => this.switchColorBackground(e)}>
                    <option value="background">{app.translator.trans('wusong8899-guaguale.admin.settings.guaguale-item-background')}</option>
                    <option value="color">{app.translator.trans('wusong8899-guaguale.admin.settings.guaguale-item-color')}</option>
                  </select>
                  <i aria-hidden="true" class="icon fas fa-sort Select-caret"></i>
                </span>

                <div id="GuaGuaLeColorContainer" class="GuaGuaLeColorAndBackground" style={GuaGuaLeColorContainerStyle}>
                  <div style="width:10px"></div>
                  <input maxlength="20" placeholder={app.translator.trans('wusong8899-guaguale.admin.settings.guaguale-item-color-placeholder')} className="FormControl" bidi={this.guagualeColor} onchange={() => this.setGuaGuaLeColorPreview()} />
                  <div style="width:5px"></div>
                  <div id="guagualeColorPreview" style={guagualeColorPreviewStyle}></div>
                </div>

                <div id="GuaGuaLeBackgroundContainer" class="GuaGuaLeColorAndBackground" style={GuaGuaLeBackgroundContainerStyle}>
                  <div style="width:10px"></div>
                  <input maxlength="255" placeholder={app.translator.trans('wusong8899-guaguale.admin.settings.guaguale-item-background-placeholder')} className="FormControl" bidi={this.guagualeImage} onchange={() => this.setGuaGuaLeColorPreview()} />
                  <div id="guagualeBackgroundPreview"></div>
                </div>
              </div>

              <div class="GuaGuaLeSettingsLabel">{app.translator.trans('wusong8899-guaguale.admin.settings.guaguale-item-settings')} <span id="winChanceText" style="font-weight: normal;font-size: 12px;">{app.translator.trans('wusong8899-guaguale.admin.guaguale-data-win-chance',{winChance:this.winChance()})}</span></div>
              
              <div style="text-align: left;">
                {this.settingType==="add" && (
                  <div style="padding-bottom:10px">
                    {app.translator.trans('wusong8899-guaguale.admin.guaguale-data-win-price')} <input oninput={() => this.GuaGuaLeWinPriceChanged()} id="winPrice" style="display:inline-block;width:100px;height: 25px;" className="FormControl" type="number" step="1" min="1" />&nbsp;
                    {app.translator.trans('wusong8899-guaguale.admin.guaguale-data-win-amount')} <input id="winAmount" style="display:inline-block;width:100px;height: 25px;" className="FormControl" type="number" step="1" min="1" />
                    <div style="display:inline-block;width:10px"></div>
                    <div style="display:inline-block;padding-top: 10px">
                      {Button.component(
                        {
                          id:"settingRatioAddButton",
                          style: "height: 25px;display: flex;align-items: center;",
                          className: 'Button Button--primary',
                          onclick: () => {
                            this.addGuaGuaLeSetting();
                          }
                        },
                        app.translator.trans('wusong8899-guaguale.admin.guaguale-data-add')
                      )}
                    </div>
                  </div>
                )}

                {this.guagualeSettingsList().map((guagualeSetting) => {
                  const guagualeSettingText = guagualeSetting.text;
                  const guagualeSettingWinPrice = guagualeSetting.winPrice;
                  const borderColor = this.getBackgroundColor(guagualeSettingWinPrice+"color");
                  const guagualeSettingsStyle = "border:2px "+borderColor+" solid;display: inline-block;margin-left: 3px;font-size: 10px;margin-bottom:3px";
                  const guagualeSettingsDeleteStyle = "cursor: pointer;border-left:2px "+borderColor+" solid;display:inline-block;padding:5px;font-size: 10px;background-color:red;color:white";

                  return (
                    <div style="display:inline-block;-webkit-user-select: none;-ms-user-select: none;user-select: none;">
                      <div style={guagualeSettingsStyle}>
                        <div style="padding:5px;display: inline-block">{guagualeSettingText}</div>
                        {this.settingType==="add" && guagualeSettingWinPrice!==0 && (
                          <div style={guagualeSettingsDeleteStyle} onclick={() => this.removeGuaGuaLeSetting(guagualeSettingWinPrice)}>
                            {app.translator.trans('wusong8899-guaguale.admin.guaguale-data-remove')}
                          </div>
                        )}
                      </div>
                    </div>
                  );
                })}
              </div>

            </div>
          </div>

          <div className="Form-group" style="text-align: center;">
            {Button.component(
              {
                className: 'Button Button--primary',
                type: 'submit',
                loading: this.loading,
              },
              this.settingType==="add"?app.translator.trans('wusong8899-guaguale.admin.guaguale-data-add'):app.translator.trans('wusong8899-guaguale.admin.guaguale-data-save')
            )}&nbsp;
            {Button.component(
              {
                className: 'Button guagualeButton--gray',
                loading: this.loading,
                onclick: () => {
                  this.hide();
                }
              },
              app.translator.trans('wusong8899-guaguale.admin.guaguale-data-cancel')
            )}
          </div>

        </div>
      </div>
    );
  }

  switchColorBackground(e){
    //
    const selectElement = e.target;
    const value = selectElement.value;
    
    if(value==="color"){ 
      $("#GuaGuaLeColorContainer").css("display","");
      $("#GuaGuaLeBackgroundContainer").css("display","none");
    }else if(value==="background"){
      $("#GuaGuaLeColorContainer").css("display","none");
      $("#GuaGuaLeBackgroundContainer").css("display","");
    }
  }

  GuaGuaLeWinPriceChanged(){
    const guagualeSettings = this.guagualeSettings;
    const guagualeSettingRatio = guagualeSettings.ratio;
    const winPriceInput = this.precisionRound($("#winPrice").val(),2);
    let winPriceExist = guagualeSettingRatio[winPriceInput]!==undefined;
    let addButtonText = app.translator.trans('wusong8899-guaguale.admin.guaguale-data-add');

    if(winPriceExist===true){
      addButtonText = app.translator.trans('wusong8899-guaguale.admin.guaguale-data-update');
    }

    $("#settingRatioAddButton .Button-label").text(addButtonText);
  }

  setGuaGuaLeColorPreview(){
    $("#guagualeColorPreview").css("background-color",this.guagualeColor());
  }

  checkGuaGuaLeAmount(){
    const guagualeAmount = parseInt(this.guagualeAmount());

    if(isNaN(guagualeAmount) || guagualeAmount<=0){
      this.guagualeAmount(this.defaultAmount);
    }

    let guagualeSettings = this.guagualeSettings;
    let guagualeSettingRatio = guagualeSettings.ratio;
    let guagualeTotalAmount = 0;

    for(let index in guagualeSettingRatio){
      const winPrice = this.precisionRound(index,2);

      if(winPrice!==0){
        const winAmount = parseInt(guagualeSettingRatio[index]);
        guagualeTotalAmount+=winAmount;
      }
    }

    if(guagualeAmount<guagualeTotalAmount){
      this.guagualeAmount(guagualeTotalAmount);
    }

    this.winChance(this.precisionRound((guagualeTotalAmount/this.guagualeAmount())*100,2));
    this.setGuaGuaLeDefaultSetting();
    $("#winChanceText").text((app.translator.trans('wusong8899-guaguale.admin.guaguale-data-win-chance',{winChance:this.winChance()}).join("")));
    m.redraw();
  }

  addGuaGuaLeSetting(){
    const winAmount = parseInt($("#winAmount").val());
    const winPrice = this.precisionRound($("#winPrice").val(),2);

    if(isNaN(winAmount) || winAmount<=0 || winPrice<=0){
      app.alerts.show(Alert, {type: 'error'}, app.translator.trans('wusong8899-guaguale.admin.guaguale-data-setting-invalid'));
      return;
    }

    const guagualeTicketLeft = this.getGuaGuaLeTicketLeft();

    if(winAmount>guagualeTicketLeft){
      app.alerts.show(Alert, {type: 'error'}, app.translator.trans('wusong8899-guaguale.admin.guaguale-data-not-enough-tickets'));
      return;
    }

    let guagualeSettings = this.guagualeSettings;

    guagualeSettings.ratio[winPrice] = winAmount;
    this.guagualeSettingsList = this.getGuaGuaLeSettingList(guagualeSettings);
    this.guagualeSettings = guagualeSettings;
    this.setGuaGuaLeDefaultSetting();
    this.checkGuaGuaLeAmount();
    $("#winAmount").val("");
    $("#winPrice").val("");
    $("#settingRatioAddButton .Button-label").text(app.translator.trans('wusong8899-guaguale.admin.guaguale-data-add'));
    m.redraw();
  }

  getGuaGuaLeTicketLeft(){
    const guagualeAmount = parseInt(this.guagualeAmount());
    let guagualeSettings = this.guagualeSettings;
    let guagualeSettingRatio = guagualeSettings.ratio;
    let guagualeTotalAmount = 0;

    for(let index in guagualeSettingRatio){
      const winPrice = this.precisionRound(index,2);

      if(winPrice!==0){
        const winAmount = parseInt(guagualeSettingRatio[index]);
        guagualeTotalAmount+=winAmount;
      }
    }

    return guagualeAmount-guagualeTotalAmount;
  }

  setGuaGuaLeDefaultSetting(){
    const guagualeTicketLeft = this.getGuaGuaLeTicketLeft();
    let guagualeSettings = this.guagualeSettings;

    guagualeSettings.ratio[0] = guagualeTicketLeft;
    this.guagualeSettingsList = this.getGuaGuaLeSettingList(guagualeSettings);
    this.guagualeSettings = guagualeSettings;

  }

  removeGuaGuaLeSetting(winPrice){
    if(winPrice!==0 && this.settingType==="add"){
      delete this.guagualeSettings.ratio[winPrice];
      this.guagualeSettingsList = this.getGuaGuaLeSettingList(this.guagualeSettings);
      this.setGuaGuaLeDefaultSetting();
      this.checkGuaGuaLeAmount();
    }
  }

  getGuaGuaLeSettingList(guagualeSettings){
    let guagualeSettingsList = [];
    let guagualeSettingRatio = guagualeSettings.ratio;

    for(let index in guagualeSettingRatio){
      let winAmount = parseInt(guagualeSettingRatio[index]);
      let winPrice = parseFloat(index);
      const winPriceText = this.moneyName.replace('[money]', winPrice);
      const unitName = app.translator.trans('wusong8899-guaguale.admin.guaguale-unit-name');
      
      guagualeSettingsList.push({text:winPriceText+" x"+guagualeSettingRatio[index]+unitName,winPrice:winPrice});
    }

    return Stream(guagualeSettingsList);
  }

  precisionRound(number, precision) {
    let factor = Math.pow(10, precision);
    return Math.round(number * factor) / factor;
  }

  getBackgroundColor(str) {
    let hash = 0;
    for (let i = 0; i < str.length; i++) {
      hash = str.charCodeAt(i) + ((hash << 5) - hash);
    }
    let colour = '#';
    for (let i = 0; i < 3; i++) {
      let value = (hash >> (i * 8)) & 0xFF;
      colour += ('00' + value.toString(16)).substr(-2);
    }
    return colour;
  }

  onsubmit(e) {
    e.preventDefault();

    this.loading = true;

    const colorBackgroundValue = $("#ColorBackgroundSelection").val();

    if(colorBackgroundValue==="color"){
      this.guagualeImage(null)
    }else if(colorBackgroundValue==="background"){
      this.guagualeColor(null)
    }

    if(this.settingType==="edit"){
      this.guagualeData.save({
        title:this.guagualeTitle(),
        desc:this.guagualeDesc(),
        cost:this.guagualeCost(),
        color:this.guagualeColor(),
        image:this.guagualeImage(),
      })
      .then(
        () => this.hide(),
        (response) => {
          this.loading = false;
          this.handleErrors(response);
        }
      );
    }else{
      app.store
        .createRecord("guagualeList")
        .save({
          title:this.guagualeTitle(),
          desc:this.guagualeDesc(),
          amount:this.guagualeAmount(),
          cost:this.guagualeCost(),
          color:this.guagualeColor(),
          image:this.guagualeImage(),
          settings:JSON.stringify(this.guagualeSettings)
        })
        .then(
          (guagualeList) => {
            location.reload();
          }
        )
        .catch((e) => {
          this.loading = false;
          this.handleErrors(guagualeList);
        });
    }
  }
}
