import Model from "flarum/Model";

export default class GuaGuaLe extends Model {}
Object.assign(GuaGuaLe.prototype, {
  id: Model.attribute("id"),
  title: Model.attribute("title"),
  desc: Model.attribute("desc"),
  color: Model.attribute("color"),
  image: Model.attribute("image"),
  amount: Model.attribute("amount"),
  purchased: Model.attribute("purchased"),
  settings: Model.attribute("settings"),
  cost: Model.attribute("cost"),
  assignedAt: Model.attribute("assigned_at"),
  activated: Model.attribute("activated"),
  guagualePurchaseData: Model.hasMany("guagualePurchaseData"),
});
