import Component from "flarum/Component";
import Button from 'flarum/components/Button';
import GuaGuaLeSettingsAddModal from './GuaGuaLeSettingsAddModal';
import GuaGuaLeSettingsDeleteModal from './GuaGuaLeSettingsDeleteModal';

export default class GuaGuaLeSettingsListItem extends Component {
  view() {
    const {guagualeData} = this.attrs;
    const moneyName = app.forum.attribute('antoinefr-money.moneyname') || '[money]';
    const guagualeID = guagualeData.id();
    const guagualeTitle = guagualeData.title();
    const guagualeDesc = guagualeData.desc();
    const guagualeAmount = guagualeData.amount();
    const guagualePurchased = guagualeData.purchased();
    const guagualeCost = guagualeData.cost();
    const guagualeCostText = moneyName.replace('[money]', guagualeCost);
    const guagualeActivated = guagualeData.activated();
    const guagualeActivatedText = guagualeActivated===0?app.translator.trans('wusong8899-guaguale.admin.settings.guaguale-item-deactivated'):app.translator.trans('wusong8899-guaguale.admin.settings.guaguale-item-activated');
    const guagualeActivatedStyle = guagualeActivated===0?"color:red":"color:green";
    const guagualeColor = guagualeData.color();
    const guagualeImage = guagualeData.image();
    const guagualeBackgroundColor = guagualeColor?guagualeColor:this.getBackgroundColor(guagualeTitle+"_"+guagualeID);
    const guagualeImageStyle = "width:200px;height:60px;background-image:url("+(guagualeImage===null?'':guagualeImage)+");background-color: "+guagualeBackgroundColor+";";
    const guagualeSettings = JSON.parse(guagualeData.settings());

    let guagualeSettingsList = [];
    const guagualeRatio = guagualeSettings.ratio;

    for(let index in guagualeRatio){
      let winAmount = parseInt(guagualeRatio[index]);
      let winPrice = parseFloat(index);
      const winPriceText = moneyName.replace('[money]', winPrice);
      const unitName = app.translator.trans('wusong8899-guaguale.admin.guaguale-unit-name');
      
      guagualeSettingsList.push({text:winPriceText+" x"+guagualeRatio[index]+unitName,winPrice:winPrice});
    }

    return (
      <div style="border: 1px dotted var(--control-color);padding: 10px;border-radius: 4px;">
        <div>
          <div style="padding-top: 5px;">
            <Button className={'Button Button--primary'} onclick={() => this.editItem(guagualeData)}>
              {app.translator.trans('wusong8899-guaguale.admin.settings.guaguale-item-edit')}
            </Button>
            &nbsp;
            <Button style="font-weight:bold;width:66px;" className={'Button Button--danger'} onclick={() => this.deleteItem(guagualeData)}>
              {app.translator.trans('wusong8899-guaguale.admin.settings.guaguale-item-delete')}
            </Button>
          </div>
          <div style="padding-top: 5px;">
            <b>{app.translator.trans('wusong8899-guaguale.admin.settings.guaguale-item-title')}: </b>
            {guagualeTitle}
          </div>
          <div style="padding-top: 5px;">
            <b>{app.translator.trans('wusong8899-guaguale.admin.settings.guaguale-item-desc')}: </b>
            {guagualeDesc}
          </div>
          <div style="padding-top: 5px;">
            <b>{app.translator.trans('wusong8899-guaguale.admin.settings.guaguale-item-id')}: </b>
            {guagualeID}&nbsp;
            <b>{app.translator.trans('wusong8899-guaguale.admin.settings.guaguale-item-cost')}: </b>
            {guagualeCostText}&nbsp;
            <b>{app.translator.trans('wusong8899-guaguale.admin.settings.guaguale-item-amount')}: </b>
            {guagualeAmount}{app.translator.trans('wusong8899-guaguale.admin.settings.guaguale-unit-name')}&nbsp;
            <b>{app.translator.trans('wusong8899-guaguale.admin.settings.guaguale-item-purchased')}: </b>
            {guagualePurchased}{app.translator.trans('wusong8899-guaguale.admin.settings.guaguale-unit-name')}&nbsp;
          </div>
          <div style="padding-top:3px;">
            <b>{app.translator.trans('wusong8899-guaguale.admin.settings.guaguale-item-settings')}: </b>
            {guagualeSettingsList.map((guagualeResult) => {
              const guagualeResultText = guagualeResult.text;
              const guagualeResultWinPrice = guagualeResult.winPrice;
              const borderColor = this.getBackgroundColor(guagualeResultWinPrice+"color");
              const guagualeResultStyle = "padding:5px;border:2px "+borderColor+" solid;display: inline-block;margin-left: 3px;font-size: 10px;margin-bottom:3px";
              
              return (
                <div style={guagualeResultStyle}>
                  {guagualeResultText}
                </div>
              );
            })}
          </div>
        </div>
        <div style="padding-top:5px;">
          <div class="GuaGuaLeImageSettingsContainer" style={guagualeImageStyle}>
          </div>
        </div>
      </div>
    );
  }

  editItem(guagualeData) {
    app.modal.show(GuaGuaLeSettingsAddModal, {guagualeData})
  }

  deleteItem(guagualeData) {
    app.modal.show(GuaGuaLeSettingsDeleteModal, {guagualeData})
  }

  getBackgroundColor(str) {
    let hash = 0;
    for (let i = 0; i < str.length; i++) {
      hash = str.charCodeAt(i) + ((hash << 5) - hash);
    }
    let colour = '#';
    for (let i = 0; i < 3; i++) {
      let value = (hash >> (i * 8)) & 0xFF;
      colour += ('00' + value.toString(16)).substr(-2);
    }
    return colour;
  }
}
